* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}
@font-face {
  font-family: "Poppins";
  src: url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;400&display=swap");
}
.main {
  min-height: 100vh;
}

/* nav */

.nav {
  min-height: 20vh;
}

.navTop {
  min-height: 15vh;
  background: #000000;
  color: white;
  display: flex;
  align-items: center;
}

.navTopLogo {
  margin: 0 1rem;
  font-size: 2rem;
  height: 15vh;
  flex: 1;
}

.navTopLogo > div {
  width: 50%;
  height: 100%;
}

.navTopLogo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.navTopEnquire {
  flex: 1;
  display: flex;
  align-items: center;
}

.navTopEnquire > div {
  flex: 1;
  display: flex;
}

.navTopEnquire > div:nth-of-type(3) {
  width: 50%;
  font-size: 1.4rem;
  justify-content: space-around;
}

.navBottom {
  min-height: 5vh;
  background: #b83b0e;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navlinks ul {
  display: flex;
  list-style: none;
}

.navlinks ul li {
  width: 8rem;
  margin: 0 1rem;
  min-height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease;
  color: white;
  cursor: pointer;
}

.navlinks ul li:hover {
  background: black;
  color: white;
}

/* page1 */
.image {
  background-image: url("./images/b1.jpg");
  height: 80vh;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  animation: slider 40s infinite linear;
}
@keyframes slider {
  15% {
    background-image: url("./images/b2.jpg");
  }
  30% {
    background-image: url("./images/b3.jpg");
  }
  45% {
    background-image: url("./images/b4.jpg");
  }
  60% {
    background-image: url("./images/b5.jpg");
  }
  75% {
    background-image: url("./images/b6.jpg");
  }
  90% {
    background-image: url("./images/b7.jpg");
  }
}

/* page2 */

.page2 {
  height: 100vh;
  color: #656e77;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.page2-title {
  display: flex;
  margin-top: 3rem;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  color: rgb(223, 37, 12);
  text-transform: uppercase;
}
.page2-content {
  width: 80%;
  margin-top: 2rem;
  font-size: 1rem;
  text-align: center;
  color: rgb(97, 100, 107);
}
.page2-button {
  margin-top: 2rem;
}
.page2-button button {
  padding: 0.6rem 1.4rem;
  border: none;
  border-radius: 5px;
  background: rgb(201, 27, 27);
  color: aliceblue;
}
.page2-images {
  height: 60vh;
  padding-top: 2rem;
  margin: auto;
  width: 80%;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.page2-images > div:nth-of-type(1) {
  grid-row: 1/3;
  grid-column: 1/3;
}

.page2-images > div {
  position: relative;
}

.page2-images > div div {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  height: 30%;
  background: rgba(255, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.page2-images > div div h3 {
  z-index: 4;
  opacity: 1;
}

.page2-images img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}
/* page3 */

.page3 {
  height: 100vh;
  width: 100%;
  margin-top: 2rem;
  background-color: rgb(4, 25, 43);
}
.page3-box {
  height: 100vh;
  width: 80%;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}
.page3-title {
  height: 10vh;
  font-size: 2rem;
  font-weight: 700;
  display: flex;
  width: auto;
  justify-content: center;
  align-items: flex-end;
  color: rgb(255, 255, 255);
}
.page3-content {
  width: 90%;
  height: 10vh;
  text-align: center;
  font-size: 0.5rem;
  margin-top: 1rem;
  display: flex;
  align-items: flex-end;
  color: #c1c2c4;
}
.page3-rest {
  height: 80vh;
  width: 90%;
  margin: 0 auto;
  display: flex;
  padding-top: 3rem;
  flex-direction: column;
}
.page3-rest-group1 {
  height: 40vh;
  width: 90%;
  margin: auto;
  display: flex;
  align-items: flex-end;
  position: relative;
}
.page3-rest-group1::after {
  content: "";
  position: absolute;
  top: 85%;
  left: 0;
  height: 0.5px;
  width: 100%;
  background-color: rgb(250, 250, 250);
}
.page3-rest1,
.page3-rest2 {
  position: relative;
}
.page3-rest1::after {
  content: "";
  position: absolute;
  top: 20%;
  left: 100%;
  height: 135%;
  width: 0.5px;
  background-color: rgb(255, 255, 255);
}
.page3-rest2::after {
  content: "";
  position: absolute;
  top: 20%;
  left: 100%;
  height: 135%;
  width: 0.5px;
  background-color: rgb(255, 255, 255);
}
.page3-rest-group2 {
  height: 40vh;
  display: flex;
  width: 90%;
  margin: auto;
}
.page3-rest1,
.page3-rest2,
.page3-rest3 {
  height: 100%;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page3-rest4,
.page3-rest5,
.page3-rest6 {
  display: flex;
}
.page3 img {
  height: 2rem;
  width: 2rem;
}
.page3-rest1-logo,
.page3-rest2-logo,
.page3-rest3-logo,
.page3-rest4-logo,
.page3-rest5-logo,
.page3-rest6-logo {
  display: flex;
  justify-content: center;
  height: 40%;
  width: 10rem;
}
.page3-rest1-sub,
.page3-rest2-sub,
.page3-rest3-sub {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.page3-rest1-sub,
.page3-rest2-sub,
.page3-rest3-sub,
.page3-rest4-sub,
.page3-rest5-sub,
.page3-rest6-sub {
  height: 10rem;
}
.page3-rest1-sub1,
.page3-rest2-sub1,
.page3-rest3-sub1,
.page3-rest4-sub1,
.page3-rest5-sub1,
.page3-rest6-sub1 {
  font-size: 1.5rem;
  font-weight: 500;
  color: rgb(255, 255, 255);
}
.page3-rest1-sub2,
.page3-rest2-sub2,
.page3-rest3-sub2,
.page3-rest4-sub2,
.page3-rest5-sub2,
.page3-rest6-sub2 {
  font-size: 1rem;
  padding-top: 1rem;
  color: rgb(192, 192, 192);
}

/* page4 */

.page4 {
  min-height: 20vh;
  width: 100%;
}
.page4-box {
  min-height: 20vh;
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.page4-title {
  height: 10vh;
  font-size: 2rem;
  font-weight: 700;
  color: rgb(17, 73, 99);
  display: flex;
  align-items: flex-end;
}
.page4-subcontent {
  margin-top: 1rem;
  text-align: center;
  line-height: 1.3rem;
  color: #636369;
}

.expertise {
  width: 100%;
  margin-top: 2rem;
}

.allExpertise {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem 0;
  flex-wrap: wrap;
}

.allExpertise > div {
  width: 20rem;
  height: 30rem;
  color: #636369;
  margin: 1rem 1rem;
  padding: 0.5rem;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.allExpertise > div img {
  width: 100%;
  height: 40%;
  object-fit: cover;
}

.allExpertise > div h3 {
  text-align: center;
  font-size: 90%;
  margin-top: 1rem;
}

.allExpertise p {
  margin: 1rem 0;
  word-break: break-all;
}

/* page5 */

.page5 {
  height: 80vh;
  width: 90%;
  margin: 2rem auto;
  display: flex;
}
.page5 > div {
  flex: 1;
  margin: 0 1rem;
}
.page5-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.page5-sideContent > div:nth-of-type(1) {
  font-size: 3rem;
  color: #3d3d68;
  margin: 0 0 2rem 0;
}

.page5-sideContent > div:nth-of-type(2) {
  line-height: 1.5rem;
}

.page5-sideContent > div:nth-of-type(3) {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}

.page5-sideContent > div:nth-of-type(3) img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin-right: 1rem;
}

.page5-sideContent > div:nth-of-type(3) > div {
  display: flex;
  margin: 1rem 0;
  align-items: center;
}

/* page 5-2  */

.ourwork {
  min-height: 100vh;
  width: 100%;
}

.ourworkTitle {
  width: 80%;
  margin: auto;
  text-align: center;
}
.ourworkTitle h1 {
  position: relative;
}

.ourworkTitle h1::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 40%;
  border: 1px solid rgb(34, 32, 32);
}
.ourworkTitle h1::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 40%;
  border: 1px solid rgb(34, 32, 32);
}

.ourWorkContent {
  width: 80%;
  min-height: 50vh;
  margin: 2rem auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.ourWorkContent > div {
  width: 15rem;
  height: 15rem;
  margin: 1rem 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
}

.ourWorkContent > div:hover img {
  width: 110%;
  height: 110%;
}

.ourWorkContent > div img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

/* page5_3 */

.totalProjects {
  min-height: 20vh;
  background: #b83b0e;
  display: flex;
  align-items: center;
}

.totalProjects > div {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
}

.totalProjects > div div {
  text-align: center;
}

.totalProjects h3 {
  font-weight: 400;
}

/* page6 */

.page6 {
  height: 100vh;
  width: 100%;
  background-color: #f2ede6;
  display: flex;
}
.page6-img {
  flex: 1;
  height: 100%;
  padding-top: 2rem;
  width: 100%;
}
.page6-img img {
  height: 40vw;
  width: 40vw;
}
.page6-content {
  flex: 1;
}
.page6-content-title {
  font-size: 5rem;
  padding-top: 1rem;
  font-weight: 600;
  padding-bottom: 3rem;
}
.page6-content-sub {
  padding-top: 3rem;
  height: 76.5%;
  margin: auto;
  border-left: 3px solid black;
  border-top: 3px solid black;
}
.page6-content-sub-sub {
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
}
.page6-content-sub1 {
  flex: 1.5;
}
.page6-content-sub2 {
  height: 85%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 5rem;
}
.page6-content-sub2 h1 {
  font-size: 2rem;
  font-weight: 500;
}
.page6-content-sub2 h2,
.page6-content-sub2 h3 {
  font-size: 1.3rem;
  font-weight: 400;
}
.page6-content-sub2-3 {
  height: auto;
  width: 80%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.page6-content-sub2-3 img {
  height: 2rem;
  width: 2rem;
}

.form-control {
  width: 280px;
  border: none;
  outline: none;
  border-bottom: 1px solid rgb(15, 12, 12);
  background: none;
  font-size: 1rem;
  font-weight: 900;
}
input,
textarea {
  height: 85px;
  color: black !important;
}
.submit-button {
  margin-top: 4rem;
  height: 3rem;
  width: 15rem;
  border-radius: 40px;
  padding: 0.5rem 4rem;
  background-color: black;
  color: white;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white !important;
  outline: none;
}
.submit {
  height: 2rem;
  width: 10rem;
  font-size: 1.2rem;
  letter-spacing: 1px;
  color: white !important;
  background-color: black;
  border: none;
  outline: none;
}

/* About page   */

.AboutPage {
  min-height: 80vh;
  width: 100%;
}

.AboutPage > div:nth-of-type(1) {
  display: flex;
  width: 90%;
  margin: 2rem auto;
}

.AboutPage > div > div {
  flex: 1;
  margin: 0 1rem;
}

.aboutImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.aboutHeading {
  font-size: 2rem;
  margin: 1rem 0;
  width: 70%;
  position: relative;
}

.aboutHeading::after {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  width: 20%;
  border: 1px solid rgb(43, 41, 41);
}

.aboutSubheading {
  font-size: 1.4rem;
  font-style: italic;
  color: #3d3d68;
  margin: 1rem 0;
}

.abouttext {
  font-size: 1.2rem;
  width: 80%;
  line-height: 2rem;
}

.AboutPage button {
  margin: 1rem 0;
  padding: 1rem 1.4rem;
  text-transform: uppercase;
  border: none;
  outline: none;
}

.visitingCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* CSS */
.button-5 {
  align-items: center;
  background-clip: padding-box;
  background-color: #fa6400;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: system-ui, -apple-system, system-ui, "Helvetica Neue", Helvetica,
    Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 1.25;
  margin: 0;
  min-height: 3rem;
  padding: calc(0.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
}

.button-5:hover,
.button-5:focus {
  background-color: #fb8332;
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
}

.button-5:hover {
  transform: translateY(-1px);
}

.button-5:active {
  background-color: #c85000;
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  transform: translateY(0);
}

/* page7 */

.page7 {
  height: 15vh;
  width: 100%;
  background-color: black;
}
.page7-content {
  height: 15vh;
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

/* our service page */

.servicePage {
  width: 100%;
  min-height: 80vh;
}

.ourServices {
  display: flex;
  height: 100%;
}

.ourServices > div:nth-of-type(1) {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ourServiceTitle > h1 {
  font-size: 3rem;
  color: #30265d;
  width: 80%;
  text-transform: uppercase;
}

.ourServices > div:nth-of-type(2) {
  width: 60%;
}

.ourServiceContent {
  display: flex;
  flex-wrap: wrap;
}

.ourServiceContent > div {
  overflow: hidden;
  width: 12rem;
  margin: 1rem;
  padding: 0 0 1rem 1rem;
  height: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.ourServiceContent > div img {
  width: 3rem;
  height: 3rem;
  object-fit: contain;
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.ourServiceContent > div:hover {
  background: #29255e;
  color: white;
  padding: 0 0 5rem 2rem;
}

.ourServiceContent > div:hover img {
  filter: invert(1);
}

.ourServiceContent > div:hover h3,
.ourServiceContent > div:hover h4 {
  color: white;
}

.ourServiceContent h3 {
  margin: 0.6rem 0;
  color: #29255e;
}
.ourServiceContent h4 {
  color: #b83b0e;
  display: none;
}

.ourServiceContent button {
  width: 5rem;
  height: 1.5rem;
  border: none;
  outline: none;
  background: white;
  color: black;
  transform: translateY(10rem);
}

.ourServiceContent > div:hover > button {
  transform: translateY(2rem);
}

.eachService {
  min-height: 50vh;
  width: 90%;
  margin: 2rem auto;
}

.aboutEachService {
  height: 20rem;
  display: flex;
  width: 100%;
  background: #f2ede6;
  margin-bottom: 5rem;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.aboutEachService > div:nth-of-type(1) {
  width: 20%;
  align-self: center;
  padding-left: 1rem;
  color: crimson;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.aboutEachService > div:nth-of-type(2) {
  width: 70%;
  margin: auto;
  line-height: 1.3rem;
}

.aboutEachService img {
  width: 5rem;
  height: 5rem;
  margin-bottom: 1rem;
  object-fit: contain;
}

.btl {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.btl > div {
  width: 18rem;
  margin: 0.6rem;
}

.gallaryDesign {
  min-height: 100vh;
  width: 100%;
}

.gallary_sections {
  width: 90%;
  margin: 2rem auto 2rem auto;
}

.gallary_sections h2 {
  text-align: center;
}

.gallary_images{
  min-height: 60vh;
  display:flex;
  flex-wrap: wrap;

}

.gallary_images >div{
  width: 15rem;
  height: 15rem;
  margin: 1rem;
}

.gallary_images > div >img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}


